// GRIDS
.simple-cards-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
    grid-gap: 30px;
}


.margin-inline {
    margin-inline: $app-margin-size;
}

.padding-inline {
    padding-inline: $app-margin-size;
}

.main-layout {
    display: grid;
    grid-template-columns: 8px 1fr 8px;
    &>* {
        grid-column: 2;
    }

    &>.full {
        grid-column: 1 / -1;
    }

    @include mq(medium) {
        grid-template-columns: 16px 1fr 16px;
    }
    
    @include mq(large) {
        grid-template-columns: 1fr 960px 1fr;
    }
}